/*--------------------------------------------------------------
# Posts Layout 5
--------------------------------------------------------------*/
.aarambha-kits-block-posts-5-container .overlay-content .blog-post-large .aarambha-kits-posts-wrapper {
  position: relative;    
}
.aarambha-kits-block-posts-5-container .overlay-content .blog-post-large .aarambha-kits-posts-wrapper .aarambha-kits-post-detail-wrap {
  position: absolute;
  z-index: 1;
  bottom: 0;
  right: 0;
  left: 0; 
}
.aarambha-kits-block-posts-5-container .aarambha-kits-posts-wrapper .aarambha-kits-post-detail-wrap {
display: -webkit-box;
display: -ms-flexbox;
display: flex;
-webkit-box-orient: vertical;
-webkit-box-direction: normal;
    -ms-flex-direction: column;
        flex-direction: column;
}
.aarambha-kits-block-posts-5-container .aarambha-kits-posts-wrapper .aarambha-kits-post-detail-wrap >*.element-order:not(:last-child) {
padding-bottom: 10px;
}

.aarambha-kits-block-posts-5-container .column >*.blog-post-list:not(:last-child) .aarambha-kits-posts-wrapper {
margin-bottom: 15px;
}

.aarambha-kits-block-posts-5-container .aarambha-kits-posts-wrapper .aarambha-kits-post-detail-wrap .entry-title {
  font-weight: 500;
  padding-bottom: 0;
}

.aarambha-kits-block-posts-5-container .aarambha-kits-posts-wrapper .aarambha-kits-post-detail-wrap .entry-categories >* {
  background-color: #289dcc;
  padding: 5px 10px;
  color: #fff;
  font-size: 12px;
  text-transform: uppercase;
  display: inline-block;
  margin-bottom: 3px;
}
.aarambha-kits-block-posts-5-container .aarambha-kits-posts-wrapper .aarambha-kits-post-detail-wrap .aarambha-kits-entry-meta .byline img{
  border-radius: 50%;
}

.aarambha-kits-block-posts-5-container .blog-post-list .aarambha-kits-posts-wrapper{
display: -webkit-box;
display: -ms-flexbox;
display: flex;
-ms-flex-wrap: wrap;
    flex-wrap: wrap;
}
.aarambha-kits-block-posts-5-container .blog-post-list .aarambha-kits-posts-wrapper .post-thumbnail-wrap {
width: 120px;
}
.aarambha-kits-block-posts-5-container .blog-post-list .aarambha-kits-posts-wrapper .aarambha-kits-post-detail-wrap {
width: 77%;
width: calc(100% - 120px);
width: -webkit-calc(100% - 120px);
}
.aarambha-kits-block-posts-5-container .aarambha-kits-posts-wrapper .aarambha-kits-post-detail-wrap:not(.overlay-content .blog-post-large .aarambha-kits-post-detail-wrap) {
background-color: #F3F3F3;
}
.aarambha-kits-block-posts-5-container .aarambha-kits-posts-wrapper .post-thumbnail-wrap .post-featured-image {
height: 100%;
}


@media only screen and (max-width: 767px){
.blog-post-large {
margin-bottom: 20px;
}


}

@media only screen and (max-width: 479px){
.aarambha-kits-block-posts-5-container .overlay-content .blog-post-large .aarambha-kits-posts-wrapper .aarambha-kits-post-detail-wrap{
  position: static;
  background: rgba(0,0,0,0.5);
}
.aarambha-kits-block-posts-5-container .blog-post-list .aarambha-kits-posts-wrapper{
  display: block;
}
.aarambha-kits-block-posts-5-container .blog-post-list .aarambha-kits-posts-wrapper .post-thumbnail-wrap{
  width: 100%;
}
.aarambha-kits-block-posts-5-container .blog-post-list .aarambha-kits-posts-wrapper .aarambha-kits-post-detail-wrap {
width: 100%;
}


}